<template>
  <div class="commonPhone" :class="{themeL:bindThemeL}">
    <!-- 头部 -->
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth" ></navigation-bar>
    <div class="commonPhoneContent">
      <div class="phoneList" v-for="(x,index) in phoneInfor" :key="index">
        <a :href="'tel:'+x.TEL">
          <div class="phoneCard clearfix">
            <img :src="x.PIC_URL" class="imgPicUrl" />
            <div class="hotPhone">
              <div class="descStyle">{{x.TEL_DESC}}</div>
              <div class="telStyle">{{x.TEL}}</div>
            </div>
            <van-image class="commonPhoneStyle" :src="phoneImg" />
            <div class="contactStyle">联系TA</div>
          </div>
        </a>
      </div>
    </div>
    <propertyNone v-show="commonPhoneDefaule && phoneInfor.length <= 0" :title="defaultTitle"></propertyNone>
  </div>
</template>

<script>
import { getPhoneListService } from "../../services/commonPhoneService";
import propertyNone from "./propertyNone";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "commonPhone",
  mounted() {
    this.getPhoneList();
  },
  data() {
    return {
      phoneInfor: [],
      commonPhoneDefaule: false,
      defaultTitle: "暂无常用电话",
      headerItemWidth:{
          width:'84px'
      },
      title:'常用电话',
      phoneImg: require("../../assets/images/personal_center/Call_small.png"),
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true
    };
  },
  components: {
    propertyNone,
    NavigationBar
  },
  methods: {
    getPhoneList() {
      let reqData = {
        OWNER_CODE: this.$userInfo.OWNER_CODE,
        AREA_CODE: this.$userInfo.AREA_CODE /*业主所在小区*/
      };
      getPhoneListService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          console.log(JSON.stringify(result));
          this.phoneInfor = result;
          this.commonPhoneDefaule = false;
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.commonPhoneDefaule = true; //是否显示default页
          this.$toast("网络异常，请稍后重试！");
        });
    },
    onClickLeft() {
      this.$router.back();
    }
  }
};
</script>

<style scoped>
.commonPhone {
  background-color: #f9f9f9;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.commonPhone .commonPhoneContent {
  padding-top: 16px;
  flex: 1;
  overflow-y: scroll;
}
.commonPhone .phoneList {
  position: relative;
  margin: 10px 5%;
  padding: 10px;
  height: 36px;
  border-radius: 5px;
  background-color: #fff;
}
.themeL .phoneList {
  height: 46px;
}
.commonPhone .phoneCard {
  margin: 3px 2.5%;
  display: flex;
}
.commonPhone .phoneCard .hotPhone {
  left: 19%;
  font-size: 16px;
  flex-grow: 1;
  margin: 0 10px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.commonPhone .phoneCard .imgPicUrl {
  float: left;
  width: 37px;
  height: 37px;
}
.themeL .imgPicUrl {
  width: 30px;
  height: 30px;
  margin-top: 3px;
}
.commonPhone .phoneCard .commonPhoneStyle {
  width: 15px;
  height: 15px;
  min-width: 15px;
  float: right;
  margin-top: 8px;
}
.themeL .phoneCard .commonPhoneStyle {
  margin-top: 15px;
}
.commonPhone .descStyle {
  font-size: 14px;
  color: #333333;
  max-width: 200px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.themeL .descStyle {
  font-size: 18px;
  height: 24px;
  line-height: 24px;
}
.commonPhone .telStyle {
  font-size: 12px;
  color: #888888;
}
.themeL .telStyle {
  font-size: 16px;
  height: 20px;
  line-height: 20px;
}
.commonPhone .contactStyle {
  font-size: 14px;
  color: #3a3a3a;
  margin-top: 6px;
  margin-left: 4px;
  min-width: 50px;
}
.themeL .contactStyle {
  font-size: 16px;
  margin-top: 12px;
}
.commonPhone .onMessage {
  padding: 20px 0;
  text-align: center;
}
</style>
